<template>
	<transition enter-active-class = 'animated fadeInRight' leave-active-class = 'animated fadeOutRight'>
    <div class="buyaic" v-show="show">
      <div class="top">
        购买AIC
        <i class="icon iconfont icon-fanhui" @click="close()"></i>
      </div>
      <div class="content">
        <div class="c-top">
          <p>价格:<span>￥{{ioItem.price}}</span></p>
        </div>
        <div class="c-c">
          <div class="c-l">
            <p>付款方式 :</p>
            <p>卖家 :</p>
            <p>交易限额 :</p>
            <p>付款期限 :</p>
            <p>卖家备注 :</p>
          </div>
          <div class="c-r">
            <p><i class="icon iconfont icon-yinhangqia"></i>&nbsp;&nbsp;银行转账</p>
            <p>{{ioItem.tel|formatTel}}</p>
            <p>{{ioItem.count}}</p>
            <p>60分钟</p>
            <div class="remark">如果你无法简洁的表达你的想法，那只说明你还不够了解它。</div>            
          </div>
        </div>
      </div>
      <div class="c-bottom">
        买入数量：<input type="text" v-model="count" @blur="checkCount()"><span class="a">AIC</span>
      </div>
      <div class="bp">
        买入金额：<span>￥{{ioItem.price*count | formatNum(4)}}</span>
      </div>
      <div class="btn" @click="showsubaic()">
        买入
      </div>
      <subaic :count='count' />
    </div>
	</transition>
</template>

<script>
import {mapGetters} from 'vuex'
import animate from 'animate.css'
import subaic from './subaic'

export default {
  components:{
    subaic
  },
  computed:{
    ...mapGetters([
      'ioItem',
      'show'
    ])
  },
  props:[
  ],
  data(){
    return{
      count:''
    }
  },
  methods:{
    close(){
      this.$store.dispatch('close')
    },
    showsubaic(){
      this.$store.dispatch('showsubaic')      
    },
    checkCount(){
      if(this.count*this.ioItem.price > this.ioItem.count){
        console.log('超出限额！')
      }
    }
  },
}
</script>

<style lang="less" scoped>
  .btn{
    margin: 0 1rem;
    height: 38px;
    font-size: 14px;
    line-height: 38px;
    border-radius: 3px;
    background-color: rgb(116,167,0);
  }
  .bp{
    color: rgb(137,143,143);
    font-size: 14px;
    font-weight: 400;
    line-height: 40px;
    height: 40px;
    text-align: left;
    margin: 0 1rem;
    span{
      padding-left: 1rem;
      color: white;
    }
  }
  .buyaic{
    background-color: rgb(21,26,29);
    position: absolute;
    left: 0;
    top: 0;
    height: 41.6875rem;
    width: 100%;
  }
  .top{
    background-color: #262d33;
    height: 46px;
    font-size: 18px;
    line-height: 46px;
    position: relative;
    .icon{
      display: block;
      font-size: 20px;
      position: absolute;
      left: 0.875rem;
      top: 0px;
    }
  }
  .content{
    background-color: #262d33;
    color: rgb(137,143,143);
    p{
      padding: 0;
      margin: 0;
    }
    margin: 1rem;
    padding: 1rem;
    .c-top{
      p{
        text-align: left;
        font-size: 16px !important;
        line-height: 23px;
        span{
          padding-left: 5px;
          font-size: 18px;
          font-weight: 700;
          color: rgb(116,167,0);
          line-height: 26px;
        }
      }
    }
    .c-c{
      display: flex;
      p{height: 1.5rem;}
      .c-l{
        flex: 1;
        text-align: right;
        font-size: 14px;
        line-height: 20px;
        p{
          padding: 5px 0;
        }
      }
      .c-r{
        flex: 3.5;
        text-align: left;
        .remark{
          margin-left: 1rem;
          font-size: 14px;
          line-height: 20px;
          padding: 5px 0
        }
        p{
          margin-left: 1rem;
          font-size: 14px;
          line-height: 20px;
          padding: 5px 0;
          .icon-yinhangqia{
            font-size: 20px;
            line-height: 14px;
            color: #f1b90c
          }
        }
      }
    }
  }
  .c-bottom{
    margin: 0 1rem;
    text-align: left;
    color: rgb(137,143,143);
    font-size: 14px;
    font-weight: 400;
    height: 40px;
    line-height: 40px;
    background-color: #262d33;
    padding-left: 1rem;
    input{
      background-color: #262d33;
      border-style: none;
      color: white;
      outline:none;
    }
    .a{
      position: absolute;
      left: 21.5rem;
    }
  }

</style>



