import Vue from 'vue'
import Router from 'vue-router'

import buyin from '../components/buyIn/buyIn'
import sellout from '../components/sellOut/sellOut'
import currenttrade from '../components/currentTrade/currentTrade'
import buyaic from '../components/buyaic/buyaic.vue'
import help from '../components/currentTrade/help.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: buyin,
    },
    {
      path: '/sellout',
      component: sellout
    },
    {
      path: '/currenttrade',
      component: currenttrade
    },
    {
      path: '/help',
      component: help
    },
  ],
  linkActiveClass:'active',
  mode:'history',
  history:true,
})



