<template>
  <div class="ct">
    <div class="item" 
      v-for="(v,k) in tradeinfo" 
      :key="k" 
      @click="showDetail(v)">
      <div class="item-top">
        <div class="it-l">
         {{v.typenode | tradeType}}
          <span>{{v.time}}</span>
        </div>
        <div class="it-r">
          <span>{{v.statustype | chosetype}}</span>
        </div>
      </div>
      <div class="item-bottom">
        <div class="ib-l">
          {{v.price | formatNum(4)}}
          <p>价格(RYC)</p>
        </div>
        <div class="ib-m">
          {{v.count | formatNum(4)}}
          <p>数量(RYC)</p>
        </div>
        <div class="ib-r">
          {{v.poundage | formatNum(4)}}
          <p>手续费(RYC)</p>
        </div>
      </div>
    </div>
    <selldetail />
    <waitconfirm />
    <waitpay />
  </div>
</template>

<script>
import subaic from '../buyaic/subaic'
import {mapGetters} from 'vuex'
import selldetail from './selldetail'
import waitconfirm from './waitconfirm'
import waitpay from './waitpay'

export default {
  methods:{
    showDetail(v){
      this.$store.dispatch('showDetail',v)
    }
  },
  computed:{
    ...mapGetters({

    })
  },
  data(){
    return{
      tradeinfo:[
        {
          typenode:0,
          time:'2017-11-12 17:36:24',
          price:0.11600000,
          count:1912.5156,
          poundage:0.0000,
          statustype:1,
        },
        {
          typenode:1,
          time:'2017-11-12 17:36:24',
          price:0.12000000,
          count:1945.5156,
          poundage:0.0000,
          statustype:2,
        },
        {
          typenode:0,
          time:'2017-11-12 17:36:24',
          price:0.11900000,
          count:1944.5156,
          poundage:0.0000,
          statustype:3,
        },
        {
          typenode:1,
          time:'2017-11-12 17:36:24',
          price:0.15500000,
          count:1965.5156,
          poundage:0.0000,
          statustype:4,
        },
      ]
    }
  },
  components:{
    subaic,
    selldetail,
    waitconfirm,
    waitpay
  }
}
</script>

<style lang="less" scoped>
  .ct{
    padding: 1rem 0;
    .item{
      line-height: 23px;
      padding: 1rem 1.5rem;
      height: 70px;
      background-color: #262d33;
      margin-bottom: 1rem;
      .item-top{
        display: flex;
        justify-content: space-between;
        .it-l{
          font-size: 16px;
          color: rgb(116,167,0);
          line-height: 23px;
          span{
            margin-left: 1rem;
            font-size: 12px;
            color: rgb(151,152,154)
          }
        }
        .it-r{
          color: rgb(241,185,12);
          font-size: 12px;
        }
      }
      .item-bottom{
        margin-top: 0.5rem;
        display: flex;
        p{
          margin: 0;padding: 0;
          font-size: 12px;
          font-weight: 400;
          color: rgb(151,152,154)
        }
        .ib-l{
          flex: 1;
          text-align: left;
          font-size: 14px;
          font-weight: 700;
          line-height: 20px;
        }
        .ib-m{
          flex: 1;
          text-align: left;
          font-size: 14px;
          font-weight: 700;
          line-height: 20px;
        }
        .ib-r{
          flex: 1;
          text-align: left;
          font-size: 14px;
          font-weight: 700;
          line-height: 20px;
        }
      }
    }
  }
</style>




