<template>
	<transition enter-active-class = 'animated fadeInRight' leave-active-class = 'animated fadeOutRight'>
    <div class="ct" v-show="hisflag">
      <div class="top">
        历史交易
        <i class="icon iconfont icon-fanhui" @click="closeHistory()"></i>
      </div>
      <div class="item" v-for="(v,k) in tradeinfo" :key="k">
        <div class="item-top">
          <div class="it-l">
            {{v.type}}
            <span>{{v.time}}</span>
          </div>
          <div class="it-r">
            <span>{{v.status}}</span>
          </div>
        </div>
        <div class="item-bottom">
          <div class="ib-l">
            {{v.price | formatNum(4)}}
            <p>价格(RYC)</p>
          </div>
          <div class="ib-m">
            {{v.count | formatNum(4)}}
            <p>数量(RYC)</p>
          </div>
          <div class="ib-r">
            {{v.poundage | formatNum(4)}}
            <p>手续费(RYC)</p>
          </div>
        </div>
      </div>
    </div>
	</transition>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  data(){
    return{
      tradeinfo:[
        {
          type:'买入',
          time:'2017-11-12 17:36:24',
          price:0.11700000,
          count:1912.5156,
          poundage:0.0000,
          status:'已成交'
        },
        {
          type:'卖出',
          time:'2017-11-12 17:36:24',
          price:0.11700000,
          count:1912.5156,
          poundage:0.0000,
          status:'已成交'
        },
        {
          type:'买入',
          time:'2017-11-12 17:36:24',
          price:0.11700000,
          count:1912.5156,
          poundage:0.0000,
          status:'已成交'
        },
        {
          type:'卖出',
          time:'2017-11-12 17:36:24',
          price:0.11700000,
          count:1912.5156,
          poundage:0.0000,
          status:'已成交'
        },
      ]
    }
  },
  computed:{
    ...mapGetters([
      'hisflag'
    ])
  },
  methods:{
    closeHistory(){
      this.$store.dispatch('closeHistory')
    }
  }
}
</script>

<style lang="less" scoped>
  .ct{
    background-color: rgb(21,26,29);
    position: absolute;
    left: 0;
    top: 0;
    height: 44.47rem;
    width: 100%;
    z-index: 100;
    .top{
      font-size: 16px;
      background-color: #262d33;
      margin-bottom: 1rem;
      .icon-fanhui{
        position: absolute;
        left: 1rem;
      }
    }
    .item{
      line-height: 23px;
      padding: 1rem 1.5rem;
      height: 70px;
      background-color: #262d33;
      margin-bottom: 1rem;
      .item-top{
        display: flex;
        justify-content: space-between;
        .it-l{
          font-size: 16px;
          color: rgb(116,167,0);
          line-height: 23px;
          span{
            margin-left: 1rem;
            font-size: 12px;
            color: rgb(151,152,154)
          }
        }
        .it-r{
          color: rgb(151, 152, 154);
          font-size: 12px;
        }
      }
      .item-bottom{
        display: flex;
        margin-top: 0.5rem;
        p{
          margin: 0;
          padding: 0;
          font-size: 12px;
          font-weight: 400;
          color: rgb(151,152,154)
        }
        .ib-l{
          flex: 1;
          text-align: left;
          font-size: 14px;
          font-weight: 700;
          line-height: 20px;
        }
        .ib-m{
          flex: 1;
          text-align: left;
          font-size: 14px;
          font-weight: 700;
          line-height: 20px;
        }
        .ib-r{
          flex: 1;
          text-align: left;
          font-size: 14px;
          font-weight: 700;
          line-height: 20px;
        }
      }
    }
  }
</style>




