<template>
	<transition enter-active-class = 'animated fadeInRight' leave-active-class = 'animated fadeOutRight'>
    <div class="selldetail" v-show="sellflag">
      <div class="top">
        AICEX
        <i class="icon iconfont icon-fanhui" @click="closesell"></i>
      </div>
      <div class="content">
        <div class="c-c">
          <div class="c-l">
            <p>交易金额 :</p>
            <p>付款方式 :</p>
            <p>开户行 :</p>
            <p>开户名 :</p>
          </div>
          <div class="c-r">
            <p>￥{{sellDe.price*sellDe.count | formatNum(4)}}</p>
            <p><i class="icon iconfont icon-yinhangqia"></i>&nbsp;&nbsp;银行转账</p>
            <p>XXXXXXXXXXXXXX</p>
            <p>李四</p>
            <!-- <p>如果你无法简洁的表达你的想法，那只说明你还不够了解它。</p>             -->
          </div>
        </div>
      </div>
      <div class="txt">买家购买凭证：</div>
      <div class="evidence">
        <div class="item"></div>
        <div class="item"></div>
        <div class="item"></div>
      </div>
      <div class="c-bottom">
        <span>请在24小时内确认收款，否则AIC将会被自动转账给买家。请及时联系买家。</span>
        <p>倒计时：</p>
      </div>
      <div class="btn">
        确认收款
      </div>
      <div class="help">
        <a href="/help">没有收到款且无法联系到买家？请联系客服</a>
      </div>
      <help />
    </div>
	</transition>
</template>

<script>
import { mapGetters } from 'vuex'
import animate from 'animate.css'
import help from './help'

export default {
  computed:{
    ...mapGetters([
      'sellflag',
      'sellDe'
    ])
  },
  components:{
    help
  },
  data(){
    return{
      
    }
  },
  methods:{
    closesell(){
      this.$store.dispatch('closesell')
    }
  }
}
</script>

<style lang="less" scoped>
.help{
  text-align: left;
  font-size: 14px;
  line-height: 20px;
  margin: 20px 17px;
  a{
    color: rgb(137,143,143);
  }
}
.txt{
  font-size: 14px;
  line-height: 20px;
  color: rgb(137,143,143);
  padding: 0 17px;
  text-align: left;
}
.evidence{
  margin: 17px;
  display: flex;
  justify-content: space-between;
  .item{
    width: 100px;
    height: 100px;
    background-color: white;
  }
}
  .btn{
    margin: 50px 17px 0 ;
    height: 38px;
    font-size: 14px;
    line-height: 38px;
    border-radius: 3px;
    background-color: rgb(116,167,0);
  }
  .selldetail{
    background-color: rgb(21,26,29);
    position: absolute;
    left: 0;
    top: 0;
    height: 41.6875rem;
    width: 100%;
  }
  .top{
    background-color: #262d33;
    height: 46px;
    line-height: 46px;
    position: relative;
    .icon{
      display: block;
      font-size: 20px;
      position: absolute;
      left: 0.875rem;
      top: 0px;
    }
  }
  .content{
    background-color: #262d33;
    color: rgb(137,143,143);
    p{
      padding: 0;
      margin: 0;
    }
    margin: 17px;
    padding: 17px;
    .c-top{
      p{
        text-align: left;
        font-size: 16px !important;
        line-height: 23px;
        span{
          padding-left: 5px;
          font-size: 18px;
          font-weight: 700;
          color: rgb(116,167,0);
          line-height: 26px;
        }
      }
    }
    .c-c{
      display: flex;
      .c-l{
        flex: 1;
        text-align: right;
        font-size: 14px;
        line-height: 20px;
        p{
          padding: 5px 0;
        }
      }
      .c-r{
        flex: 3.5;
        text-align: left;
        p:first-child{
          font-size: 18px;
          font-weight: 700;
          color: rgb(234,0,114)
        }
        p{
          margin-left: 10px;
          font-size: 14px;
          line-height: 20px;
          padding: 5px 0;
          .icon-yinhangqia{
            font-size: 20px;
            line-height: 14px;
          }
        }
      }
    }
  }
  .c-bottom{
    margin: 0 17px;
    height: 40px;
    text-align: left;
    span,p{
      line-height: 20px;
      color: rgb(234,0,114);
      font-size: 14px;
      font-weight: 400;
    }
  }

</style>



