// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './components/index'

Vue.config.productionTip = false
Vue.filter(
  "chosetype",function(num){
    if(num==1){
      return ' 等待您付款'
    }
    if(num==2){
      return ' 等待买家付款'
    }
    if(num==3){
      return ' 等待卖家确认收款'
    }
    if(num==4){
      return ' 等待您确认收款'
    }
  }
)
Vue.filter(
    "formatNum",function(num,count){
      return num.toFixed(count)
    }
)
Vue.filter(
  "tradeType",function(num){
    if(num==0){
      return '买入'
    }
    if(num==1){
      return '卖出'
    }
  }
)
Vue.filter(
  'formatTel',function(num){
    return  num.toString().replace(/(\d{3})\d{4}(\d{4})/, '$1****$2');
  }
)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



