const state = {
    show:false,
    ioItem:'',
    subflag:'',
    de:'',
    sc:''
}

const getters = {
    show(state){
        return state.show
    },
    ioItem(state){
        return state.ioItem
    },
    subflag(state){
        return state.subflag
    },
    de(state){
        return state.de
    },
    sc(state){
        return state.sc
    }
}

const actions = {
    open({commit},v){
        commit('open',v)
    },
    close({commit}){
        commit('close')
    },
    showsubaic({commit}){
        commit('showsubaic')
    },
    cb({commit}){
        commit('cb')
    }
}

const mutations = {
    open(state,v){
        state.show=true
        state.ioItem = v
    },
    close(state){
        state.show=false
    },
    showsubaic(state){
        state.subflag=true
    },
    cb(state){
        state.subflag=false
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


