<template>
  <div class="top">
    <div class="top-m">
      <span>C2C交易</span>
    </div>
    <span class="ht" @click="showHistory()">历史交易</span>
    <history />
  </div>
</template>

<script>
import history from '../history/history'

export default {
  components:{
    history
  },
  methods:{
    showHistory(){
      this.$store.dispatch('showHistory')
    }
  }
}
</script>

<style lang="less" scoped>
  .top{
    background-color: #262d33;
    height: 46px;
    line-height: 46px;
    .top-m{
      font-size: 18px;
      text-align: center;
    }
    .ht{
      position: relative;
      top: -46px;
      right: -9.5rem;
      font-size: 14px;
    }
  }
</style>



