<template>
  <div class="buyin">
    <div class="item" 
      v-for="(v,k) in info" 
      :key="k" 
      @click="open(v)">
      <div class="item-l">{{v.tel | formatTel}}</div>
      <div class="item-m">
        <p>{{v.count}}</p>
        <p>可买(AIC)</p>
      </div>
      <div class="item-r">
        <p>￥{{v.price}}</p>
        <p>卖价(RMB)</p>
        <i class="icon iconfont icon-sandian"></i>
      </div>
    </div>
    <buyaic />
    <!-- <history /> -->
    <!-- <subaic/> -->
    <!-- <waitpay/> -->
    <!-- <waitconfirm/> -->
    <!-- <selldetail/> -->
    <!-- <help/> -->
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import buyaic from '../buyaic/buyaic'
import history from '../history/history'
import subaic from '../buyaic/subaic'
import waitpay from '../currentTrade/waitpay'
import waitconfirm from '../currentTrade/waitconfirm'
import selldetail from '../currentTrade/selldetail'
import help from '../currentTrade/help'

export default {
  methods:{
    open(v){
      this.$store.dispatch('open',v)
    }
  },
  data(){
    return{
      info:[
        {
          tel:15813885588,
          count:3500,
          price:15.12,
        },
        {
          tel:13813885388,
          count:3400,
          price:16.12,
        },
        {
          tel:13855661388,
          count:4400,
          price:16.12,
        },
        {
          tel:13683885228,
          count:3300,
          price:17.58,
        },
        {
          tel:13955664889,
          count:5100,
          price:15.82,
        },
      ]
    }
  },
  components:{
    buyaic,
    history,
    subaic,
    waitpay,
    waitconfirm,
    selldetail,
    help
  },
}
</script>

<style lang="less" scoped>
.buyin{
  background-color: #101010;
  padding-top:15px;
  .item{
    height: 67px;
    background-color: #262d33;
    margin-bottom: 15px;
    display: flex;
    p{
      padding: 0;
      margin: 0;
    }
    .item-l{
      flex: 1;
      line-height: 67px;
      font-size: 16px;
      font-weight: 700;
      font-style: normal;
    }
    .item-m{
      flex: 1;
      text-align: left;
      padding-top: 15px;
      p:nth-child(1){
        line-height: 20px;
        font-size: 14px;
        font-weight: 700;
        font-style: normal;
        padding-left: 20px;
      }
      p:nth-child(2){
        color: rgb(151,152,154);
        font-size:12px;
        line-height: 20px;
        padding-left: 20px;
      }
    }
    .item-r{
      flex: 1;
      height: 67px;
      text-align: left;
      padding-top: 15px;
      p:nth-child(1){
        line-height: 20px;
        font-size: 14px;
        font-weight: 700;
        font-style: normal;
        color: rgb(116,167,0);
        padding-left: 20px;
      }
      p:nth-child(2){
        color: rgb(151,152,154);
        line-height: 20px;
        font-size:12px;
        padding-left: 20px;
      }
      .icon{
        position: relative;
        float: right;
        top: -35px;
        font-size: 25px;
        color:rgb(151,152,154);
      }
    }
  }
}
</style>




