<template>
	<transition enter-active-class = 'animated fadeInRight' leave-active-class = 'animated fadeOutRight'>
    <div class="subaic" v-show="subflag">
      <div class="top">
        完成付款
        <i class="icon iconfont icon-fanhui" @click="cb()"></i>
      </div>
      <div class="content">
        <div class="c-c">
          <div class="c-l">
            <p>您需支付 :</p>
            <p>付款方式 :</p>
            <p>开户行 :</p>
            <p>开户名 :</p>
            <p>卖家备注 :</p>
          </div>
          <div class="c-r">
            <p>{{ioItem.price*count | formatNum(4)}}</p>
            <p><i class="icon iconfont icon-yinhangqia"></i>&nbsp;&nbsp;银行转账</p>
            <p>XXXXXXXXXXXXXX</p>
            <p>李四</p>
            <div class="remark">如果你无法简洁的表达你的想法，那只说明你还不够了解它。</div>            
          </div>
        </div>
      </div>
      <div class="c-bottom">
        <span>请在60分钟内完成支付并上传支付凭证，否则交易将会自动取消。（请上传真实凭证，否则将封号处理！）</span>
        <div class="timeout">
          <span>倒计时：<span id="result"></span></span>
        </div>
      </div>
      <div class="upload">
        <input type="file" 
               id='pic'
               @change='changeFile($event,this)' 
               accept="image/jpeg,image/png,">
        <img :src="img.data" id="showImg" alt="22">
        <i class="icon iconfont icon-zhaoxiangji" 
           id="camera" 
           @click="openFile()"></i>
      </div>
      <div class="btn" @click="checkTime()">
        完成付款
      </div>
    </div>
	</transition>
</template>

<script>
import {mapGetters} from 'vuex'
import animate from 'animate.css'


export default {
  computed:{
    ...mapGetters([
      'subflag',
      'ioItem'
    ])
  },
  props:[
    'count',
  ],
  data(){
    return{
      img:{
        data:''
      },
      tm:'3600'
    }
  },
  methods:{
    cb(){
      this.$store.dispatch('cb')
    },
    //上传图片
    changeFile(e,i){
      let img = this.img , file = e.target , reader = new FileReader()
      reader.readAsDataURL(file.files[0])
      reader.onload = function(){
        img.data = this.result
        console.log(this.result)
      }
    },
    //点击打开图片
    openFile(){
      let pic = document.getElementById('pic')
      pic.click()
      let camera = document.getElementById('camera')
      camera.style.opacity = 0
    },

    //倒计时
    checkTime(){
      var interval;
      var djs = function(){
        if(!!interval) return;
        var result = document.getElementById('result')
        var d = new Date("1111/1/1,0:0:10");
        result.innerHTML = d.getHours() + "时" + d.getMinutes() + "分" + d.getSeconds() + "秒";
        interval = setInterval(function(){
          if(d.getHours() ==0 && d.getMinutes()==0 && d.getSeconds()==0) {
            clearInterval(interval);
            interval =null;
          }
          result.innerHTML = d.getHours() + "时" + d.getMinutes() + "分" + d.getSeconds() + "秒";
          d.setSeconds(d.getSeconds() - 1);
        },1000);
      }    
    djs()
    }
  }
}
</script>

<style lang="less" scoped>
#showImg{
  width: 5rem;
  height: 5rem;
}
.upload{
  margin: 1rem;
  width: 5rem;
  height: 5rem;
  background-color: white;
  text-align: center;
  input{
    display: none;
  }
  .icon{
    position: absolute;
    top: 23.6rem;
    left: 2.6rem;
    font-size: 24px;
    line-height: 70px;
    color: rgb(137,143,143);
  }
}
  .btn{
    margin: 0 17px;
    height: 38px;
    font-size: 16px;
    line-height: 38px;
    border-radius: 3px;
    background-color: rgb(116,167,0);
  }
  .subaic{
    background-color: rgb(21,26,29);
    position: absolute;
    left: 0;
    top: 0;
    height: 41.6875rem;
    width: 100%;
  }
  .top{
    background-color: #262d33;
    height: 46px;
    font-size: 18px;
    line-height: 46px;
    position: relative;
    .icon{
      display: block;
      font-size: 20px;
      position: absolute;
      left: 0.875rem;
      top: 0px;
    }
  }
  .content{
    background-color: #262d33;
    color: rgb(137,143,143);
    p{
      padding: 0;
      margin: 0;
    }
    margin: 1rem;
    padding: 1rem;
    .c-top{
      p{
        text-align: left;
        font-size: 16px !important;
        line-height: 23px;
        span{
          padding-left: 5px;
          font-size: 18px;
          font-weight: 700;
          color: rgb(116,167,0);
          line-height: 26px;
        }
      }
    }
    .c-c{
      display: flex;
      p{height: 1.5rem}
      .c-l{
        flex: 1;
        text-align: right;
        font-size: 14px;
        line-height: 20px;
        p{
          padding: 5px 0;
        }
      }
      .c-r{
        flex: 3.5;
        text-align: left;
        .remark{
          margin-left: 1rem;
          font-size: 14px;
          line-height: 20px;
          padding: 5px 0
        }
        p:first-child{
          font-size: 18px;
          font-weight: 700;
          color: rgb(234,0,114)
        }
        p{
          margin-left: 1rem;
          font-size: 14px;
          line-height: 20px;
          padding: 5px 0;
          .icon-yinhangqia{
            font-size: 20px;
            line-height: 14px;
          }
        }
      }
    }
  }
  .c-bottom{
    margin: 0 1rem;
    // height: 40px;
    text-align: left;
    span{
      line-height: 20px;
      color: rgb(234,0,114);
      font-size: 14px;
      font-weight: 400;
    }
  }

</style>



