const state = {
    sellflag:false,
    comflag:false,
    payflag:false,
    sellDe:'',
    hisflag:false,
}

const getters = {
    sellflag(state){
        return state.sellflag
    },
    comflag(state){
        return state.comflag
    },
    payflag(state){
        return state.payflag
    },
    sellDe(state){
        return state.sellDe
    },
    hisflag(state){
        return state.hisflag
    },
}

const actions = {
    showDetail({commit},v){
        commit('showDetail',v)
    },
    closesell({commit}){
        commit('closesell')
    },
    closecom({commit}){
        commit('closecom')
    },
    closepay({commit}){
        commit('closepay')
    },
    showHistory({commit}){
        commit('showHistory')
    },
    closeHistory({commit}){
        commit('closeHistory')
    },
}

const mutations = {
    showHistory(state){
        state.hisflag = true
    },
    closeHistory(state){
        state.hisflag = false
    },
    showDetail(state,v){
        state.sellDe=v
        while(v.statustype==4 || v.statustype==2){
            return state.sellflag = true
        }
        while(v.statustype==3){
            return state.comflag = true
        }
        while(v.statustype==1){
            return state.payflag = true
        }
    },
    closesell(state){
        state.sellflag=false
    },
    closecom(state){
        state.comflag=false
    },
    closepay(state){
        state.payflag=false
    },

}

export default {
    state,
    getters,
    actions,
    mutations
}


